<template>
  <div class="container">
    <div class="header">
      <div class="search">
        <el-input placeholder="请输入内容" v-model="searchInput" class="input-with-select">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
      <div class="page">
        <el-pagination
          background
          layout="prev, pager, next"
          :total="100">
        </el-pagination>
      </div>
    </div>
    <!--  -->
    <div class="table">
      <el-table
        :data="tableData"
        style="width: 100%">
        <el-table-column
          label="ID"
          width="70">
          <template slot-scope="scope">
            <span size="medium">{{ scope.row.id }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="标题"
          width="180">
          <template slot-scope="scope">
            <span size="medium">{{ scope.row.title }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="导航"
          width="170">
          <template slot-scope="scope">
            <span size="medium">{{ scope.row.navigation_name }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="分类"
          width="170">
          <template slot-scope="scope">
            <span size="medium">{{ scope.row.category_name }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="创建时间"
          width="280">
          <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.create_time }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作"
          width="180">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleEdit(scope.row)">Edit</el-button>
            <el-popconfirm title="确定删除？" @onConfirm="delArticle">
              <el-button style="margin-left: 10px;" size="mini" type="danger" @click="handleDelete(scope.row)" slot="reference">Delete</el-button>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <router-view></router-view> -->
  </div>
</template>

<script>
  import { GetArticle } from '@/api/api'
  import { DelArticle } from '@/api/api'
  import util from '../../../utils/utils'

  export default {
    data() {
      return {
        searchInput: '',
        select: '',
        tableData: [],
        articleId: ''
      }
    },
    mounted() {
      this.getArticle() // 获取文章列表
    },
    methods: {
      handleEdit(article) {
        this.$router.push({
          path:'/admin/',
          query: {article: article, type: 'editor'}
        });
        util.$emit('editorTabActive')
      },
      handleDelete(article) { 
        this.articleId = article.id
      },
      delArticle() {
        DelArticle(this.articleId).then(response => {
          if (response.data.code === 0 && response.data.msg === "ok") {
            this.successMsg()
            this.getArticle()
          } else {
            this.errMsg('', response.data.msg)
          }
        }).catch(err => {
          this.errMsg('', err)
        })
      },
      // 获取分类
      getArticle() {
        GetArticle().then(response => {
          if (response.data.code === 0 && response.data.msg === "ok") {
            if (response.data.data.article == null) {
              this.tableData = []
              return
            }
            this.tableData = response.data.data.article
            return
          }
        }).catch(err => {
          this.errMsg('获取文章列表失败 ', err)
        })
      },
      // 提示信息
      successMsg() {
        this.$message({
          type: 'success',
          message: '操作成功'
        });
      },
      errMsg(msg, err) {
        this.$message({
          type: 'error',
          message: msg + err
        });
      },
    }
  }
</script>

<style scoped>
.container {
  width: 100%;
}  
.header {
  width: 400px;
  margin-left: auto;
  margin-right: 80px
}
.search {
  width: 100%;
  margin-left: 10px
}
.page {
  width: 100%;
  margin-top: 10px
}

.table {
  margin-left: 80px
}

.el-select .el-input {
   width: 130px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
</style>
